.interactive-session .interactive-response .chat-used-context-list.chat-thinking-items {
	color: var(--vscode-descriptionForeground);
	padding-top: 0;
}

.interactive-session .interactive-response .value .chat-thinking-box {
	outline: none;
	position: relative;
	color: var(--vscode-descriptionForeground);

	.chat-used-context {
		margin: 0px;
	}

	.monaco-button.hidden,
	.chat-pinned-preview.hidden {
		display: none;
	}

	.chat-used-context-list.chat-thinking-collapsible {
		border: 1px solid var(--vscode-chat-requestBorder);
		border-radius: 4px;
		margin-bottom: 0;
		position: relative;
		overflow: hidden;

		.chat-tool-invocation-part {
			.chat-used-context {
				margin-bottom: 0px;
				margin-left: 2px;
			}

			.progress-container {
				margin: 0 0 2px 6px;
			}
		}

		.chat-thinking-item.markdown-content {
			padding: 3px 12px 4px 24px;
			position: relative;
			font-size: var(--vscode-chat-font-size-body-s);

			.progress-container {
				margin-bottom: 0px;
				padding-top: 0px;
			}

			/* chain of thought lines */
			&::before {
				content: '';
				position: absolute;
				left: 10.5px;
				top: 0px;
				bottom: 0px;
				width: 1px;
				border-radius: 0;
				background-color: var(--vscode-chat-requestBorder);
				mask-image: linear-gradient(to bottom, #000 0 7px, transparent 7px 19px, #000 19px 100%);
			}

			&:first-child::before {
				mask-image: linear-gradient(to bottom, transparent 0 19px, #000 19px 100%);
			}

			&:last-child::before {
				mask-image: linear-gradient(to bottom, #000 0 7px, transparent 7px 100%);
			}

			&:only-child::before,
			&:only-child::after {
				background: none;
				mask-image: none;
			}

			&:only-child {
				padding-inline-start: 10px;
			}

			&::after {
				content: '';
				position: absolute;
				left: 8px;
				top: 10px;
				width: 6px;
				height: 6px;
				border-radius: 50%;
				background-color: var(--vscode-chat-requestBorder);
			}
		}
	}

	.chat-thinking-item {
		padding: 6px 12px;
		position: relative;

		.progress-container {
			margin-bottom: 0px;
			padding-top: 0px;
		}
	}

	.chat-thinking-text {
		font-size: var(--vscode-chat-font-size-body-s);
		padding: 0 10px;
		display: block;
	}

	.rendered-markdown > p {
		margin: 0;
	}
}

.interactive-session .interactive-response .value .chat-thinking-fixed-mode {
	outline: none;

	&.chat-used-context-collapsed .chat-used-context-list.chat-thinking-collapsible.chat-thinking-streaming {
		max-height: 200px;
		overflow: hidden;
		display: block;
	}

	&:not(.chat-used-context-collapsed) .chat-used-context-list.chat-thinking-collapsible.chat-thinking-streaming {
		max-height: none;
		overflow: visible;
		display: block;
	}

	.chat-used-context-list.chat-thinking-collapsible:not(.chat-thinking-streaming) {
		max-height: none;
		overflow: visible;
	}
}

.editor-instance .interactive-session .interactive-response .value .chat-thinking-box .chat-thinking-item ::before {
	background: var(--vscode-editor-background);
}
